ue;\n        border-radius: 50%;\n        transition: .2s all;\n\n        &:after {\n            width: 3px;\n            height: 7px;\n            border: solid white;\n            border-width: 0 2px 2px 0;\n            transform: rotate(45deg);\n            content: '';\n            position: absolute;\n            display: none;\n        }\n\n        &:hover {\n            border: 2px solid transparent;\n            background-image: linear-gradient(white, white), radial-gradient(circle at top left, hsl(192, 100%, 67%),hsl(280, 87%, 65%));\n            background-origin: border-box;\n            background-clip: padding-box, border-box;\n        }\n    }\n\n    & input:checked ~ .checkbox__checkmark {\n        background: colors.$check-background;\n        border: 0;\n    }\n\n    & input:checked ~ .checkbox__checkmark:after {\n        display: block;\n        top: 7px;\n        left: 10px;\n    }\n}\n\n.border-gradient {\n}\n.border-gradient-purple {\n}\n\n@mixin button {\n    background: 0;\n    border: 0;\n    font-size: 1em;\n}"],"sourceRoot":""}]);
